<template>
  <div class="modal-dialog">
    <form role="form" @submit.prevent="onSubmit">
      <div class="modal-content">
        <div class="modal-header">
          <h4 class="modal-title">New Partner</h4>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="partner_name">Name</label>
            <input
              type="text"
              class="form-control"
              v-model="partner_name"
              id="partner_name"
              placeholder="Enter partner's Name"
              autocomplete="off"
            />
            <span class="error invalid-feedback" data-error=""></span>
          </div>
        </div>
        <div class="modal-footer justify-content-between">
          <button
            type="button"
            class="btn btn-default"
            data-dismiss="modal"
            @click="clearFields()"
          >
            Cancel
          </button>
          <button type="submit" class="btn btn-primary">Save changes</button>
        </div>
      </div>
    </form>
    <!-- /.modal-content -->
  </div>
</template>

<script>
import PartnerService from "./partnerService";
import AlertService from "../alertService";
const partnerService = new PartnerService();
const alertService = new AlertService();
export default {
  name: "PartnerForm",
  props: {
    editingPartner: Object,
  },
  data() {
    return {
      partner_id: null,
      partner_name: "",
      partner_owner: null,
      messageError: [],
    };
  },
  methods: {
    onSubmit: function () {
      const partner = {
        id: this.partner_id,
        name: this.partner_name,
        owner: this.partner_owner,
      };

      partnerService
        .writePartner(partner)
        .then((res) => {
          if (partner.id == null) {
            if (res.data.data.createPartner.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.createPartner.message,
                "success"
              );
              this.$emit("partnerCreated", res.data.data.createPartner.partner);
            } else {
              alertService.showToast(
                res.data.data.createPartner.message,
                "error"
              );
            }
          } else {
            if (res.data.data.updatePartner.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.updatePartner.message,
                "success"
              );
              this.$emit("partnerCreated", res.data.data.updatePartner.partner);
            } else {
              alertService.showToast(
                res.data.data.updatePartner.message,
                "error"
              );
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    clearFields: function () {
      this.partner_id = null;
      this.partner_name = "";
      this.partner_owner = null;
    },
  },
  watch: {
    editingPartner(partner) {
      this.partner_id = partner.id;
      this.partner_name = partner.name;
      this.partner_owner = partner.owner;
    },
  },
};
</script>

