<template>
  <div class="modal-dialog">
    <form role="form" @submit.prevent="onSubmit">
      <div class="modal-content">
        <div class="modal-header">
          <h4 class="modal-title">New Gift</h4>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="gift_libel">Libel</label>
            <input
              type="text"
              class="form-control"
              v-model="gift_libel"
              id="gift_libel"
              placeholder="Enter gift's libel"
              autocomplete="off"
            />
            <span class="error invalid-feedback" data-error=""></span>
          </div>
        </div>
        <div class="modal-footer justify-content-between">
          <button
            type="button"
            class="btn btn-default"
            data-dismiss="modal"
            @click="clearFields()"
          >
            Cancel
          </button>
          <button type="submit" class="btn btn-primary">Save changes</button>
        </div>
      </div>
    </form>
    <!-- /.modal-content -->
  </div>
</template>

<script>
import GiftService from "./giftService";
import AlertService from "../alertService";

const giftService = new GiftService();
const alertService = new AlertService();
export default {
  name: "GiftForm",
  props: {
    editingGift: Object,
    owner: Number,
  },
  data() {
    return {
      gift_id: null,
      gift_libel: "",
      messageError: [],
    };
  },
  methods: {
    onSubmit: function () {
      const gift = {
        id: this.gift_id,
        name: this.gift_libel,
        owner: this.owner,
      };

      giftService
        .writeGift(gift)
        .then((res) => {
          if (gift.id == null) {
            if (res.data.data.createGift.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.createGift.message,
                "success"
              );
              this.$emit("giftCreated", res.data.data.createGift.gift);
            } else {
              alertService.showToast(res.data.data.createGift.message, "error");
            }
          } else {
            if (res.data.data.updateGift.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.updateGift.message,
                "success"
              );
              this.$emit("giftCreated", res.data.data.updateGift.gift);
            } else {
              alertService.showToast(res.data.data.updateGift.message, "error");
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    clearFields: function () {
      this.gift_id = null;
      this.gift_libel = "";
    },
  },
  watch: {
    editingGift(gift) {
      this.gift_id = gift.id;
      this.gift_libel = gift.name;
    },
  },
};
</script>

