<template>
  <div>
    <div class="container-fluid">
      <div class="row">
        <div class="col-6">
          <div class="card card-success card-outline">
            <div class="card-header">
              <h3 class="card-title">Partners</h3>
              <div class="card-tools">
                <ul class="nav nav-pills ml-auto">
                  <li class="nav-item">
                    <button
                      type="button"
                      class="btn btn-block btn-success"
                      data-toggle="modal"
                      data-target="#addPartnerForm"
                    >
                      Add Partner
                    </button>
                  </li>
                </ul>
              </div>
            </div>
            <!-- /.card-header -->
            <div class="card-body table-responsive p-0">
              <table class="table table-head-fixed text-nowrap">
                <thead>
                  <tr>
                    <th></th>
                    <th>#</th>
                    <th>Name</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(partner, index) in partners"
                    v-bind:item="partner"
                    :index="index"
                    :key="partner.id"
                  >
                    <td>
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          :id="partner.id"
                          @click="checkPartner(partner, $event)"
                        />
                      </div>
                    </td>
                    <td>{{ index + 1 }}</td>
                    <td>{{ partner.name }}</td>
                    <td>
                      <i
                        class="fas fa-edit"
                        style="cursor: pointer"
                        @click="editPartner(partner)"
                      ></i>
                      ¦
                      <i
                        class="fas fa-trash"
                        style="cursor: pointer"
                        @click="deletePartner(partner)"
                      ></i>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <!-- /.card-body -->
          </div>
        </div>
        <div class="col-6">
          <GiftList :partnerGiftToDisplay="partnerGiftToDisplay"> </GiftList>
        </div>
        <div class="modal fade" id="addPartnerForm">
          <PartnerForm
            @partnerCreated="addPartner"
            :editingPartner="editingPartner"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Swal from "sweetalert2";
import PartnerService from "./partnerService";
import AlertService from "../alertService";
import PartnerForm from "./PartnerForm.vue";
import GiftList from "./Gift.vue";

const partnerService = new PartnerService();
const alertService = new AlertService();
export default {
  name: "Partner",
  components: { PartnerForm, GiftList },
  props: {},
  data() {
    return {
      partners: [],
      establishmentTypes: [],
      partnerGiftToDisplay: null,
      editingPartner: null,
      idcheckbox: null,
    };
  },
  mounted() {
    partnerService
      .getPartners()
      .then((res) => {
        this.partners = res.data.data.partners;
        if (this.partners.length > 0) {
          this.partnerGiftToDisplay = this.partners[0];
        }
      })
      .catch((err) => console.error(err));
  },
  methods: {
    addPartner: function (partner) {
      if (this.partners.find((n) => n.id === partner.id)) {
        const index = this.partners.findIndex((n) => n.id === partner.id);
        this.partners.splice(index, 1, partner);
      } else {
        this.partners.unshift(partner);
      }
    },
    editPartner: function (partner) {
      this.editingPartner = partner;
      //$("#addPartnerForm").modal("show");
    },
    deletePartner: function (partner) {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#28a745",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      }).then((result) => {
        if (result.isConfirmed) {
          partnerService
            .deletePartner(partner)
            .then((res) => {
              if (res.data.data.deletePartner.result == true) {
                this.partners = this.partners.filter(
                  (n) => n.id !== partner.id
                );
                alertService.showToast(
                  res.data.data.deletePartner.message,
                  "success"
                );
              } else {
                alertService.showToast(
                  res.data.data.deletePartner.message,
                  "error"
                );
              }
            })
            .catch((err) => console.error(err));
        }
      });
    },
    checkPartner: function (partner, event) {
      if (event.target.checked) {
        if (this.idcheckbox != null) {
          document.getElementById(this.idcheckbox).checked = false;
        }
        this.idcheckbox = partner.id;
        this.partnerGiftToDisplay = partner;
      } else {
        document.getElementById(partner.id).checked = false;
        this.idcheckbox = null;
        this.partnerGiftToDisplay = null;
      }
    },
  },
};
</script>