import axios from 'axios'
import AxiosConfig from '../axiosConfig.js'

const axiosConfig = new AxiosConfig();
export default class GiftService {
    async getGifts(partnerId) {
        const data = await axios({
            method: "POST",
            url: axiosConfig.getbaseURL(),
            data: {
                query: `
                query($partnerId: Int!){
                    giftByPartner(partnerId:$partnerId){
                        id
                        name
                        owner{
                            id
                        }
                    }
                }`,
                variables: {
                    partnerId: partnerId
                }
            }
        });
        return data
    }

    async writeGift(gift) {
        if (gift.id == null) {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                    mutation ($name: String!, $owner: Int!) {
                        createGift (
                            name: $name,
                            owner: $owner
                        ){
                            gift {
                                id
                                name
                                owner{
                                    id
                                }
                            }
                            message
                            result
                        }
                    }`,
                    variables: {
                        name: gift.name,
                        owner: gift.owner
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data;
        } else {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                    mutation ($id:Int!, $name: String!, $owner: Int!) {
                        updateGift(
                        giftID: $id,
                            name: $name,
                            owner: $owner
                        ){
                            gift {
                                id
                                name
                                owner{
                                    id
                                }
                            }
                            message
                            result
                        }
                    }`,
                    variables: {
                        id: gift.id,
                        name: gift.name,
                        owner: gift.owner
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data;
        }
    }

    async deleteGift(gift) {
        const body = {
            query: `
                mutation ($id:Int!) {
                    deleteGift(
                    giftId: $id
                    ){
                        message
                        result
                    }
                }`,
            variables: {
                id: gift.id
            }
        }

        const data = await axios.post(axiosConfig.getbaseURL(), body, {
            headers: axiosConfig.getheaders()
        });
        return data
    }
}