<template>
  <div>
    <div class="card card-success card-outline">
      <div class="card-header">
        <h3 class="card-title" v-if="partnerGiftToDisplay">
          {{ partnerGiftToDisplay.name }}
        </h3>
        <h3 class="card-title" v-else>Gifts</h3>
        <div class="card-tools">
          <ul class="nav nav-pills ml-auto">
            <li class="nav-item">
              <button
                type="button"
                class="btn btn-block btn-success"
                data-toggle="modal"
                data-target="#addGiftForm"
              >
                Add Gift
              </button>
            </li>
          </ul>
        </div>
      </div>
      <!-- /.card-header -->
      <div class="card-body table-responsive p-0">
        <table class="table table-head-fixed text-nowrap">
          <thead>
            <tr>
              <th>#</th>
              <th>libel</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(gift, index) in allGifts"
              v-bind:item="gift"
              :index="index"
              :key="gift.id"
            >
              <td>{{ index + 1 }}</td>
              <td>{{ gift.name }}</td>
              <td>
                <i
                  class="fas fa-edit"
                  style="cursor: pointer"
                  @click="editGift(gift)"
                ></i>
                ¦
                <i
                  class="fas fa-trash"
                  style="cursor: pointer"
                  @click="deleteGift(gift)"
                ></i>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- /.card-body -->
    </div>
    <div
      class="modal fade"
      id="addGiftForm"
      v-if="partnerGiftToDisplay != null"
    >
      <GiftForm
        @giftCreated="addGift"
        :editingGift="editingGift"
        :owner="parseInt(partnerGiftToDisplay.id)"
      />
    </div>
  </div>
</template>
<script>
import Swal from "sweetalert2";
import AlertService from "../alertService";
import GiftService from "./giftService";
import GiftForm from "./GiftForm.vue";

const alertService = new AlertService();
const giftService = new GiftService();
export default {
  name: "GiftList",
  components: { GiftForm },
  props: {
    partnerGiftToDisplay: Object,
  },
  data() {
    return {
      editingGift: null,
      allGifts: [],
    };
  },
  mounted() {
    if (this.partnerGiftToDisplay !== null) {
      this.getData(parseInt(this.partnerGiftToDisplay.id));
    }
  },
  methods: {
    addGift: function (gift) {
      if (this.allGifts.find((n) => n.id === gift.id)) {
        const index = this.allGifts.findIndex((n) => n.id === gift.id);
        this.allGifts.splice(index, 1, gift);
      } else this.allGifts.unshift(gift);
    },
    editGift: function (gift) {
      this.editingGift = gift;
      //$("#addGiftForm").modal("show");
    },
    deleteGift: function (gift) {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#28a745",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      }).then((result) => {
        if (result.isConfirmed) {
          giftService
            .deleteGift(gift)
            .then((res) => {
              if (res.data.data.deleteGift.result == true) {
                this.allGifts = this.allGifts.filter((n) => n.id !== gift.id);
                alertService.showToast(
                  res.data.data.deleteGift.message,
                  "success"
                );
              } else {
                alertService.showToast(
                  res.data.data.deleteGift.message,
                  "error"
                );
              }
            })
            .catch((err) => console.error(err));
        }
      });
    },
    getData: function (partnerId) {
      giftService
        .getGifts(partnerId)
        .then((res) => {
          this.allGifts = res.data.data.giftByPartner;
        })
        .catch((err) => console.error(err));
    },
  },
  watch: {
    partnerGiftToDisplay(owner) {
      if (owner !== null) {
        this.getData(parseInt(owner.id));
      } else {
        this.allGifts = [];
      }
    },
  },
};
</script>