import axios from 'axios'
import AxiosConfig from '../axiosConfig.js'

const axiosConfig = new AxiosConfig();
export default class PartnerService {
    async getPartners() {
        const data = await axios({
            method: "POST",
            url: axiosConfig.getbaseURL(),
            data: {
                query: `{ 
                    partners{
                        id
                        name
                    }
                }`
            }
        });
        return data
    }

    async writePartner(partner) {
        if (partner.id == null) {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                    mutation ($name: String!) {
                        createPartner (
                            name: $name
                        ){
                            partner {
                                id
                                name
                            }
                            message
                            result
                        }
                    }`,
                    variables: {
                        name: partner.name
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data;
        } else {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                    mutation ($id: Int!, $name: String!) {
                        updatePartner (
                          partnerID: $id,
                            name: $name
                        ){
                            partner {
                                id
                                name
                            }
                            message
                            result
                        }
                    }`,
                    variables: {
                        id: partner.id,
                        name: partner.name
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data;
        }
    }

    async deletePartner(partner) {
        const body = {
            query: `
                mutation($id: Int!){
                    deletePartner (
                        partnerId: $id
                    ){
                        message
                        result
                    }
                }`,
            variables: {
                id: partner.id
            }
        }

        const data = await axios.post(axiosConfig.getbaseURL(), body, {
            headers: axiosConfig.getheaders()
        });
        return data
    }
}